    // Production only
     import { Configuration, getConfigurationJson } from "./Configuration";

    let settings = null;
    //let breadscrumbSetting = null;
    if (process.env.NODE_ENV !== 'production') {
        settings = Configuration;
        //breadscrumbSetting = versionConfig;
        routePath();
    } else {
        // Production only
         const promise = getConfigurationJson();
         promise.then(result => {
            settings = result.Configuration;
            routePath();
         });
    }

    function routePath() {
        // const offersPart = '/offers';
        //const pathParts = window.location.pathname.split(offersPart);
        const currentBrowser = detectBrowser();
        const currentOS = detectOS();
        const currentDevice = detectDevice() ? 'Mobile' : detectTab() ? 'Tab' : 'Web';


        var loanAmount = getParam("loanAmount");
        var loanPurpose = getParam("loanPurpose");
        var partnerid = getParam("partnerid");
        var gclid = getParam("gclid");
        var featured = getParam("featured");
        var programid = getParam("programid");
        var affiliateid = getParam("affiliateid");
        var campaignid = getParam("campaignid");
        var utmSource = getParam("utm_source");
        var utmMedium = getParam("utm_medium");
        var utmTerm = getParam("utm_term");
        var utmCampaign = getParam("utm_campaign");
        var utmContent = getParam("utm_content");
        var dealerid = getParam("dealerid");
        // var email = getParam("email");
        var version = getParam("version");
        // var isLoading = true;
        var waitTime = 1000;

        //ReactDOM.render(<LoadingAnimation isLoading={true}/>, document.getElementById('root'));
        setTimeout(() => {
            //isLoading = false;
            
            if(window.location.pathname.toLocaleLowerCase().indexOf('/personal-loans/app-page-1') > -1){
                ReactDOM.render(<Breadcrumb loanAmount={isValidInteger(loanAmount) && loanAmount !== '' ? loanAmount : window.loanAmount} 
                loanPurpose={loanPurpose ? loanPurpose : window.loanPurpose} 
                configSettings={settings} 
               // breadscrumbSetting={breadscrumbSetting}
                currentBrowser={currentBrowser} 
                GCLId={gclid ? gclid : ''} 
                PartnerId={partnerid ? partnerid : window.partnerId} 
                Featured={featured ? featured : window.featured}
                currentOS={currentOS}
                currentDevice={currentDevice}
                programid={programid}
                affiliateid={affiliateid ? affiliateid: window.affiliateid}
                campaignid={campaignid}
                utmSource={utmSource}
                utmMedium={utmMedium}
                utmTerm={utmTerm}
                utmCampaign={utmCampaign}
                utmContent={utmContent}
                dealerid={dealerid}
                version={version}
                IsHeadWay={window.isHeadway === "1" || window.isHeadway === 1 ? true : false}
                PostData={window.postData}
                />, document.getElementById('root'));
                HideLoadingPage();
                callGoogleTagManager();
                 /*
                CallGA();
                 */
            }  
        }, waitTime);    
    
    }

    //function hideLoder() {
        //isLoading = false;
        //this.setState({ isLoading: false });
    //}
    function detectBrowser() {
        var browser = (function(agent){
            switch(true){
                case agent.indexOf("edge") > -1: return "edge";
                case agent.indexOf("opr") > -1 && !!window.opr: return "opera";
                case agent.indexOf("chrome") > -1 && !!window.chrome: return "chrome";
                case agent.indexOf("trident") > -1: return "ie";
                case agent.indexOf("firefox") > -1: return "firefox";
                case agent.indexOf("safari") > -1: return "safari";
                default: return "other";
            }
        })(window.navigator.userAgent.toLowerCase());

        return browser;
    }
    function detectOS() {
        var browser = (function(agent){
            switch(true){
                case agent.indexOf("windows nt 10.0") > -1 : return "Windows 10";
                case agent.indexOf("windows nt 6.2") > -1 : return "Windows 8";
                case agent.indexOf("windows nt 6.1") > -1 : return "Windows 7";
                // case agent.indexOf("windows nt 6.1") > -1 : return "Windows Vista";
                case agent.indexOf("windows nt 5.1") > -1 : return "Windows XP";
                case agent.indexOf("windows nt 5.0") > -1 : return "Windows 2000";
                case agent.indexOf("android") > -1: return "Android";
                case agent.indexOf("mac") > -1: return "Mac/iOS";
                case agent.indexOf("x11") > -1: return "UNIX";
                case agent.indexOf("linux") > -1: return "Linux";
                default: return "other";
            }
        })(window.navigator.userAgent.toLowerCase());

        return browser;
    }
